
export const userValidation = {
  created() {
    console.log(this.$ls.get('currentUser'))
    if (this.$ls.get('currentUser').role !== 0) {
      // router after component is init
      this.$nextTick(() => {
        this.$router.replace('/login')
      })
    }
  }
}



